ht: 600;\n    border: none;\n    border-bottom: 2px solid white;\n    padding: 0.3em;\n    color: $white;\n    background-color: $color-primary;\n  }\n  .modal__note-content {\n    border: 2px solid white;\n    border-radius: 0.5em;\n    font-size: 1em;\n    padding: 0.5em;\n    text-align: justify;\n    white-space: pre-wrap;\n    overflow-wrap: break-word;\n    word-wrap: break-word;\n  }\n  .modal__btn {\n    display: flex;\n    align-self: flex-end;\n    gap: 1em;\n    button {\n      border-radius: 0.5em;\n      padding: 0.5em;\n      font-size: 1em;\n    }\n  }\n  .modal__delete-btn {\n    background-color: transparent;\n    color: $red;\n    border: 1px solid $red;\n    &:hover {\n      background-color: $red;\n      color: $white;\n    }\n    &::selection {\n      background-color: $red;\n      color: $white;\n    }\n  }\n  .modal__save-btn {\n    background-color: transparent;\n    color: $green;\n    border: 1px solid $green;\n    &:hover {\n      background-color: $green;\n      color: $white;\n    }\n    &::selection {\n      background-color: $green;\n      color: $white;\n    }\n  }\n  .modal__exit-btn {\n    background-color: transparent;\n    color: $gray;\n    border: 1px solid gray;\n    &:hover {\n      background-color: $gray;\n      color: $white;\n    }\n    &::selection {\n      background-color: $gray;\n      color: $white;\n    }\n  }\n}\n","@import './note-modal';\n\n.notes {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  flex-direction: column;\n  padding: 1.5em;\n\n  &__list {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    list-style: none;\n    gap: 1em;\n    width: 100%;\n    &-item {\n      display: flex;\n      flex-direction: column;\n      gap: 0.5em;\n      padding: 1em;\n      background-color: $color-note;\n      text-align: justify;\n      border-radius: 0.5em;\n      cursor: pointer;\n      width: 80%;\n\n      .note__title {\n        font-size: 1.5em;\n        font-weight: 600;\n      }\n      .note__date {\n        font-weight: 300;\n      }\n      .note__content {\n        white-space: wrap;\n        overflow: hidden;\n        text-overflow: ellipsis;\n        max-height: 1em;\n      }\n\n      .note__modal {\n        display: none;\n        position: fixed;\n        z-index: 1;\n        left: 0;\n        top: 0;\n        width: 100%;\n        height: 100%;\n        overflow: auto;\n        background-color: $backdrop-modal;\n      }\n      .note__modal--open {\n        display: flex;\n        align-items: center;\n        justify-content: center;\n      }\n    }\n\n    @mixin priority {\n      border-left: 5px solid;\n    }\n    .low-priority {\n      @include priority();\n      border-color: $green;\n    }\n    .medium-priority {\n      @include priority();\n      border-color: $yellow;\n    }\n    .high-priority {\n      @include priority();\n      border-color: $red;\n    }\n\n    .blank-priority {\n      @include priority();\n      border-color: $white;\n    }\n  }\n  &__add-btn {\n    background-color: $color-note;\n    padding: 0.75em;\n    border-radius: 50%;\n    border: 0px transparent;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    cursor: pointer;\n    margin-top: 1em;\n    &:hover {\n      background-color: $green;\n    }\n  }\n}\n\n.add-icon {\n  height: 1.5em;\n  width: 1.5em;\n  color: $white;\n}\n",".form__modal {\n  display: none;\n  position: fixed;\n  z-index: 1;\n  left: 0;\n  top: 0;\n  width: 100%;\n  height: 100%;\n  overflow: auto;\n  background-color: $backdrop-modal;\n}\n.form__modal--open {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n.form__container {\n  display: flex;\n  flex-direction: column;\n  padding: 1em;\n  gap: 1em;\n  width: 90%;\n  border-radius: 0.5em;\n  background-color: $color-primary;\n  border: 1px solid white;\n}\n.title-input {\n  border: none;\n  padding: 0.5em;\n  background-color: $color-note;\n  color: $white;\n  font-size: 1.25em;\n  border-bottom: 1px solid white;\n  &::placeholder {\n    color: $white;\n    font-size: 1.25em;\n    font-family: $font-family_1;\n    font-weight: 400;\n  }\n}\n#note-content {\n  resize: none;\n  border: 0px transparent;\n  border-radius: 0.5em;\n  font-size: 1em;\n  padding: 1em;\n  &::placeholder {\n    font-family: $font-family_1;\n    font-size: 0.9em;\n  }\n}\n.form__label {\n  font-weight: 600;\n  font-size: 1.5em;\n  text-align: justify;\n}\n.form-priority {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  border-radius: 1em;\n}\n.form-priority__btn {\n  padding: 0.5em;\n  flex-grow: 1;\n  background-color: $color-note;\n  border: 0px transparent;\n  font-size: 1em;\n}\n#low {\n  border-top-left-radius: 1em;\n  border-bottom-left-radius: 1em;\n  color: $green;\n  &:hover {\n    transition: 0.2s;\n    background-color: $green;\n    color: $black;\n  }\n}\n#low.form-priority__btn--selected {\n  transition: 0.2s;\n  background-color: $green;\n  color: $black;\n}\n#high {\n  border-top-right-radius: 1em;\n  border-bottom-right-radius: 1em;\n  color: $red;\n  &:hover {\n    transition: 0.2s;\n    background-color: $red;\n    color: $black;\n  }\n}\n#high.form-priority__btn--selected {\n  transition: 0.2s;\n  background-color: $red;\n  color: $black;\n}\n#medium {\n  &:hover {\n    transition: 0.2s;\n    background-color: $yellow;\n    color: $black;\n  }\n  color: $yellow;\n  border-left: 1px solid black;\n  border-right: 1px solid black;\n}\n#medium.form-priority__btn--selected {\n  transition: 0.2s;\n  background-color: $yellow;\n  color: $black;\n}\n.confirm__btn {\n  align-self: center;\n  background-color: $color-primary;\n  color: $white;\n}\n.confirm__icon {\n  border: 1px solid $color-note;\n  padding: 0.2em;\n  border-radius: 50%;\n  &:hover {\n    color: $green;\n    background-color: $color-note;\n    transition: 0.2s;\n  }\n}\n","@import './abstracts/colors';\n@import './abstracts/base';\n@import './components/header';\n@import './components/notes';\n@import './components/form-modal';\n\n@media only screen and (min-width: 765px) {\n  .notes__list {\n    flex-direction: row;\n    flex-wrap: wrap;\n    align-items: center;\n    justify-content: center;\n  }\n  .notes__list-item {\n    width: 40%;\n  }\n  .modal__content {\n    width: 60%;\n  }\n  .form__container {\n    width: 60%;\n  }\n}\n@media only screen and (min-width: 1400px) {\n  .notes__list-item {\n    width: 25%;\n  }\n  .modal__content {\n    width: 30%;\n  }\n  .form__container {\n    width: 30%;\n  }\n}\n"],"sourceRoot":""}]);
